import { Injectable } from '@angular/core';
import { Http } from '@angular/http';

import { Observable } from 'rxjs/Observable';

import 'rxjs/add/operator/catch'; 
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/publishReplay';
import { WeatherData } from '../weather/weather-data';
@Injectable()
export class WeatherService {
  weather: Observable <WeatherData>;
  constructor(private http: Http) { }

   getLatestWeather(): Observable <WeatherData> {
      //if(!this.weather){
     return this.http.get('http://marsweather.ingenology.com/v1/latest/')
      .map((response) => response.json().report)
      .publishReplay(1) //caching
      .refCount() //caching
      .do(data => console.log("weather data:", data))
      .catch((error: any) => {
        console.log(`An error occurred ${error}`);
        return Observable.throw('Something bad happened with weather data; please check the console');
      });
      //console.log("what is this.weather: ",this.weather)
    //  return this.weather;
  //}
   }
}



