import { Component, OnInit } from '@angular/core';
import { MenuItem } from '../nav/menu-item';



@Component({
  selector: 'navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {
  menuItems: MenuItem[];
  title = 'Exploration Mars';

  constructor() { }

  ngOnInit() {
    this.menuItems = [
      { caption: 'Map', link: ['/map']},
      { caption: 'Rovers', link: ['/rovers']},
      { caption: 'Orbiter', link: ['/orbiter']},
      { caption: 'Weather', link: ['/weather']}
    ];
  }

}



