import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { MapComponent } from './map/map.component';

import { NavbarComponent } from './nav/navbar.component';
import { DailyImageComponent } from './daily-image/daily-image.component';
import { DailyImageService } from './image-services/daily-image.service';
import { WeatherService } from './image-services/weather.service';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule } from "@angular/router";
import { AppRoutingModule, routableComponents } from "./app-routing.module";

import { MdToolbarModule } from '@angular/material';
import { FlexLayoutModule } from '@angular/flex-layout';
import { MdChipsModule } from '@angular/material';
import { MdGridListModule } from '@angular/material';
import { MdSelectModule } from '@angular/material';
import { OrbiterImageService } from "app/image-services/orbiter-image.service";

@NgModule({
  declarations: [
    AppComponent,
    MapComponent,
     NavbarComponent,
    DailyImageComponent,
    routableComponents
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    AppRoutingModule,
    RouterModule,
    BrowserAnimationsModule,
    MdToolbarModule,
    FlexLayoutModule,
    MdChipsModule,
    MdGridListModule,
    MdSelectModule
  ],
  providers: [DailyImageService, WeatherService, OrbiterImageService],
  bootstrap: [AppComponent]
})
export class AppModule { }



