import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import * as moment from 'moment';

import { Observable } from 'rxjs/Observable';

import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/reduce';
import { RoverData } from "../rover-data";
import { RoverManifest } from "../rover-manifest";

@Injectable()
export class DailyImageService {

  private apikey = 'aamDUaS02IdR3OFDGpYmk9l8CfOWju70ailvCaKF';
  //https://api.nasa.gov/mars-photos/api/v1/rovers/curiosity/photos?earth_date=2015-6-3&api_key=DEMO_KEY
  private rover = 'curiosity';
  private yesterdayEarthDate = moment().subtract(3, 'days').format('YYYY-MM-DD');

  private baseUrl = 'https://api.nasa.gov/mars-photos/api/v1/rovers/';
  currentRoverData: RoverData;
  currentManifestData: RoverManifest;

  constructor(private http: Http) { }

  getDailyImage(name, camera, sol): Observable<RoverData[]> {
    return this.http.get(`${this.baseUrl}${name}/photos?sol=${sol}&camera=${camera}&api_key=${this.apikey}`)
      .map((response, index) => this.extractImageData(response))
      .do(data => console.log("rover data:", data))
      .catch((error: any) => {
        console.log(`An error occurred ${error}`);
        return Observable.throw('Something bad happened with images; please check the console');
      });
  }

  private extractImageData<T>(res: any) {
    if (res.status < 200 || res.status >= 300) {
      throw new Error('Bad response status: ' + res.status);
    }

    let rd = res.json().photos.map((r, a) => {
      this.currentRoverData = {
        image_src: r.img_src,
        earth_date: r.earth_date,
        rover: r.rover.name,
        cam: r.camera.full_name
      }
      return this.currentRoverData;
    })
    return rd;
  }

  getRoverManifest(roverName): Observable<RoverManifest> {
    return this.http.get(`https://api.nasa.gov/mars-photos/api/v1/manifests/${roverName}?api_key=${this.apikey}`)
      .map((response) => {
       let rm = this.extractManifestData(response);
       console.log("RM:",rm);
       return rm;
      })
      .do(data => console.log("rover manifest:", data))
      .catch((error: any) => {
        console.log(`An error occurred ${error}`);
        return Observable.throw('Something bad happened with weather data; please check the console');
      });
}

private extractManifestData(res: any): RoverManifest {
    if (res.status < 200 || res.status >= 300) {
      throw new Error('Bad response status: ' + res.status);
    }
      let numPhotos = res.json().photo_manifest.photos.length
      let rm = res.json().photo_manifest;
      let latestPhotos = rm.photos[numPhotos - 1];
        this.currentManifestData = {
          name: rm.name,
          sol: latestPhotos.sol,
          cameras: latestPhotos.cameras
        }
        console.log("extract manifestData",this.currentManifestData)
      return this.currentManifestData;
  }

}


