import { Component, OnInit } from '@angular/core';
import { WeatherService } from '../image-services/weather.service';
import { WeatherData } from "app/weather/weather-data";
import { Subscription } from "rxjs/Subscription";

@Component({
  selector: 'app-weather',
  templateUrl: './weather.component.html',
  styleUrls: ['./weather.component.css']
})
export class WeatherComponent implements OnInit {
  wData: Subscription;
  latestWeatherData: WeatherData;
  constructor(private weatherService: WeatherService) { }

  ngOnInit() {
    this.latestWeatherData = new WeatherData();
    this.getLatestWeather();
  }

  getLatestWeather(){
    
    console.log("weatherService:::",this.weatherService)
   this.weatherService.getLatestWeather().subscribe(
      latestWeatherData => { 
        this.latestWeatherData = latestWeatherData;
       // this.wData.unsubscribe();
      },
      (errorMsg: string) => {
        console.log("there's a problem here with weather data:", errorMsg);
      }
    );
  }
}



