import { Component, OnInit } from '@angular/core';
import { OrbiterImageService } from "app/image-services/orbiter-image.service";
import { Observable } from "rxjs/Observable";
import { OrbiterData } from "app/orbiter/orbiter-data";

@Component({
  selector: 'app-orbiter',
  templateUrl: './orbiter.component.html',
  styleUrls: ['./orbiter.component.css']
})
export class OrbiterComponent implements OnInit {
  attribution: string =  "NASA/JPL/University of Arizona";
  orbiterImageData: OrbiterData[];
  constructor(private orbiterImageService: OrbiterImageService) { }

  ngOnInit() {
    this.getOrbiterImages ()
  }

  getOrbiterImages(){
    this.orbiterImageService.getImages().subscribe(
      orbiterImageData => {
        this.orbiterImageData = orbiterImageData.orbiter
        console.log("this.orbiterImageData: ", this.orbiterImageData)
      }
    )
  }

}



