import { Component, OnInit, Output, EventEmitter, Input, SimpleChanges } from '@angular/core';
import { DailyImageService } from '../image-services/daily-image.service';
import { RoverData } from '../rover-data';
import { Observable } from "rxjs/Observable";
import { RoverManifest } from "../rover-manifest";

@Component({
  selector: 'daily-image',
  templateUrl: './daily-image.component.html',
  styleUrls: ['./daily-image.component.css']
})
export class DailyImageComponent {
  roverSelectedOption:string = 'Opportunity';
  selectedValue: string;
  @Output() onRoverSelected = new EventEmitter<string>();
  @Output() onCameraSelected = new EventEmitter<string>();
  
  @Input() manifestData:RoverManifest;
  @Input() roverDatas: Observable<RoverData[]>;
  
  constructor(private imageService: DailyImageService) { }

  ngOnInit() {
    this.manifestData = new RoverManifest();
  }

  onCameraSelect(selectedCam: any) {
    this.onCameraSelected.emit(selectedCam.value);
  }

  onRoverSelect(selectedRover: any) {
    this.onRoverSelected.emit(selectedRover.value);
  }
}



