import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/map';
import { Observable } from "rxjs/Observable";
import { OrbiterData } from "app/orbiter/orbiter-data";

@Injectable()
export class OrbiterImageService {

  constructor(private http: Http) { }

  getImages() {
    return this.http.get("../assets/orbiter.json")
      .map(res => res.json())
      .do(data => console.log("orbiter: ",data));
  }
}



