import { Component, OnInit } from '@angular/core';
import { DailyImageService } from "../image-services/daily-image.service";
import { RoverManifest } from "app/rover-manifest";
import { Observable } from "rxjs/Observable";
import { RoverData } from "app/rover-data";

@Component({
  selector: 'rovers',
  templateUrl: './rovers.component.html',
  styleUrls: ['./rovers.component.css']
})
export class RoversComponent implements OnInit {
  manifestData:RoverManifest;
  defaultRover:string = "Opportunity";
  roverDatas: Observable<RoverData[]>;
  
  constructor(private imageService: DailyImageService) { }

  ngOnInit() {
    this.getInitialRoverManifest(this.defaultRover);
  }

  onRoverSelected(rover:string) {
    this.getRoverManifest(rover);
  }

  getRoverManifest(currentRover) {
    this.imageService.getRoverManifest(currentRover).subscribe(
      manifestData => this.manifestData = manifestData
    )
  }

  getInitialRoverManifest(currentRover) {
    this.imageService.getRoverManifest(currentRover).subscribe(
      manifestData => {this.manifestData = manifestData
        console.log("manifest",manifestData)
      this.getDailyImage(this.manifestData.name, this.manifestData.cameras[0], this.manifestData.sol)
      return manifestData}
    )
  }

  onCameraSelected(cam:string) {
    this.getDailyImage(this.manifestData.name, cam, this.manifestData.sol)
  }

  getDailyImage(name, selectedCam, sol) {
    this.roverDatas = this.imageService.getDailyImage(name, selectedCam, sol);
  }
}



